<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
    
        <el-form-item label="计划名称">
          <el-input
            v-model="listQuery.plan_name"
            placeholder="请输入"
          ></el-input>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span> 列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <!-- 新增物料弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="600px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
    
        <el-form-item label="计划名称" prop="plan_name">
          <el-input v-model="dialogForm.plan_name"></el-input>
        </el-form-item>
        <el-form-item label="计划产量" prop="plan_count">
          <el-input v-model="dialogForm.plan_count"></el-input>
        </el-form-item>
        <el-form-item label="计划开工日" prop="plan_start_day">
          <el-date-picker
            v-model="dialogForm.plan_start_day"
            type="date"
            style="width: 100%"
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
            placeholder="选择日期"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="计划完工日" prop="plan_end_day">
          <el-date-picker
            v-model="dialogForm.plan_end_day"
            type="date"
            style="width: 100%"
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
            placeholder="选择日期"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="备注" prop="description">
          <el-input type="textarea" v-model="dialogForm.description"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";
export default {
  name: "ProductionPlan",
  components: {
    ELTable,
  },
  data() {
    return {
      // 筛选
      listQuery: {
        prodect_code: "",
        plan_name: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        prodect_code: "",
        plan_name: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示

      // 新增 弹出层
      dialogTitle: "",
      dialogFlag: false,

      dialogForm: {
        prodect_code: "",
        product_plan_code: "",
        plan_name: "",
        plan_count: "",
        plan_start_day: "",
        plan_end_day: "",
        description: "",
      },
      dialogFormBase: {
        prodect_code: "",
        product_plan_code: "",
        plan_name: "",
        plan_count: "",
        plan_start_day: "",
        plan_end_day: "",
        description: "",
      },
      dialogFormRules: {
        prodect_code: [
          { required: true, message: "请选择产品", trigger: "change" },
        ],
        plan_name: [
          { required: true, message: "请输入计划名称", trigger: "blur" },
        ],
        plan_count: [
          { required: true, message: "请输入计划产量", trigger: "blur" },
        ],
        plan_start_day: [
          { required: true, message: "请选择计划开工日", trigger: "blur" },
        ],
        plan_end_day: [
          { required: true, message: "请选择计划完工日", trigger: "blur" },
        ],
      },

      // 表格列
      tableCol: [
        {
          label: "计划内码",
          field: "product_plan_code",
          align: "center",
        },
        {
          label: "计划名称",
          field: "plan_name",
          align: "center",
        },
        // {
        //   label: "产品名称",
        //   field: "product_name",
        //   align: "center",
        // },
        {
          label: "计划产量",
          field: "plan_count",
          align: "center",
        },
        {
          label: "计划开工日",
          field: "plan_start_day",
          align: "center",
        },
        {
          label: "计划完工日",
          field: "plan_end_day",
          align: "center",
        },
        {
          label: "备注",
          field: "description",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },
  computed: {
    ...mapState(["ProductCodeNameList"]),
  },
  methods: {
    ...mapActions(["getLogisticProductCodeName"]),

    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticProductPlanDeal(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticProductPlanDeal(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticProductPlanDeal(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticProductPlanDeal(this.listQuery);
    },
    // 生产计划列表 查询

    async getLogisticProductPlanDeal(params) {
      baseManagement.getLogisticProductPlanDeal(params).then((res) => {
        console.log("生产计划列表 查询", res);
        if (res.code == 0) {
          if (res.data.data && res.data.data.length > 0) {
            let tableData = res.data.data;
          
            this.tableData = tableData;
            this.listQuery.total = res.data.total;
          }
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    

    // 生产计划列表 新增
    postLogisticProductPlanDeal(params) {
      baseManagement.postLogisticProductPlanDeal(params).then((res) => {
        console.log("生产计划列表 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticProductPlanDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 生产计划列表 新增
    addBtn() {
      this.dialogTitle = "新增生产计划";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },

    // 生产计划列表 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改生产计划";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = {
        prodect_code: rowData.prodect_code,
        product_plan_code: rowData.product_plan_code,
        plan_name: rowData.plan_name,
        plan_count: rowData.plan_count,
        plan_start_day: rowData.plan_start_day,
        plan_end_day: rowData.plan_end_day,
        description: rowData.description,
      };
    },

    // 生产计划列表 修改
    getLogisticPutProductPlanDeal(params) {
      baseManagement.getLogisticPutProductPlanDeal(params).then((res) => {
        console.log("生产计划列表 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticProductPlanDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 弹出层确认按钮
    dialogSave() {
      console.log(this.dialogTitle);
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增生产计划") {
            console.log("新增生产计划", this.dialogForm);
            this.postLogisticProductPlanDeal(this.dialogForm);
          } else if (this.dialogTitle == "修改生产计划") {
            console.log("修改生产计划", this.dialogForm);
            this.getLogisticPutProductPlanDeal(this.dialogForm);
          }
        }
      });
    },

    // 生产计划列表 删除
    getLogisticDeleteProductPlanDeal(params) {
      baseManagement.getLogisticDeleteProductPlanDeal(params).then((res) => {
        console.log("生产计划列表 删除", res);
        if (res.code == 0) {
          this.getLogisticProductPlanDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        console.log(123);
        this.getLogisticDeleteProductPlanDeal({
          product_plan_code: rowData.product_plan_code,
        });
      });
    },
  },
  mounted() {
    // 生产计划列表 查询
    this.getLogisticProductPlanDeal(this.listQuery);
  },
};
</script>
<style lang="scss" scoped>
</style>